import { Inline, InlineProperties } from '@ali/4ever-cangjie';
import { ELEMENT_TYPE } from '../../utils/meta';

export interface ReferProperties {
  data?: InlineProperties['data'];
  nodes?: InlineProperties['nodes'];
}

export default class Refer extends Inline {
  static TYPE = ELEMENT_TYPE;

  static create(properties: Refer | ReferProperties | string) {
    if (typeof properties === 'string') {
      throw new Error('`Refer.create` does not accept string');
    }
    if (Refer.isRefer(properties)) {
      return properties;
    }
    return super.create({
      ...properties,
      type: Refer.TYPE,
    }) as Refer;
  }

  static isRefer(maybeRefer: any): maybeRefer is Refer {
    return Inline.isInline(maybeRefer) && maybeRefer.type === Refer.TYPE;
  }
}
